import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

export default function ProductsPage() {
  const products = [
    { sku: "P001", name: "น้ำปลา", stock: 24, unit: "ขวด", min: 10 },
    { sku: "P002", name: "ซีอิ๊วขาว", stock: 2, unit: "ขวด", min: 8 },
    { sku: "P003", name: "น้ำมันพืช", stock: 4, unit: "ขวด", min: 6 },
  ];

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold">สินค้า</h1>
          <p className="text-sm text-slate-500">จัดการรายการสินค้า</p>
        </div>

        <Button className="rounded-xl">+ เพิ่มสินค้า</Button>
      </div>

      <Card className="rounded-2xl">
        <CardContent className="p-4">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>SKU</TableHead>
                <TableHead>ชื่อสินค้า</TableHead>
                <TableHead className="text-right">คงเหลือ</TableHead>
                <TableHead>หน่วย</TableHead>
                <TableHead className="text-right">ขั้นต่ำ</TableHead>
                <TableHead>สถานะ</TableHead>
              </TableRow>
            </TableHeader>

            <TableBody>
              {products.map((p) => {
                const low = p.stock <= p.min;
                return (
                  <TableRow key={p.sku}>
                    <TableCell className="font-medium">{p.sku}</TableCell>
                    <TableCell>{p.name}</TableCell>
                    <TableCell className="text-right font-semibold">
                      {p.stock}
                    </TableCell>
                    <TableCell>{p.unit}</TableCell>
                    <TableCell className="text-right">{p.min}</TableCell>
                    <TableCell>
                      {low ? (
                        <Badge variant="destructive">ใกล้หมด</Badge>
                      ) : (
                        <Badge variant="secondary">ปกติ</Badge>
                      )}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
